<template>
    <div class="black-bg" v-if="show">
        <div class="white-bg">
            <button class="close" @click="closeP">X</button>
            <div class="quiz">
                <img :src="require('@/assets/mission/bg-10.png')" class="img">
            </div>
            <div class="content">
                <div class="cam-container">
                    <div @click="triggerCamera" class="cam-button mb-3">
                        <svg-icon type="mdi" :path="path"></svg-icon>
                    </div>
                    <input type="file" name="photofile" id="photofile" accept="image/*" capture="camera"
                    @change="onPhotoFileChange">
                </div>
                <br>
                <div class="img-container text-center">
                    <img src="" id="photoimg">
                </div>
                <div class="button-container">
                    <v-btn color="primary" class="text-center mt-3 pl-10 pr-10" @click="submitResponse"></v-btn>
                </div>
            </div>
        </div>
        <v-dialog v-model="dialog" max-width="500">
            <v-card class="pa-3">
                <v-card-text>
                <v-row align="center" justify="center">
                    <v-col align="center">
                    <div class="img-container text-center">
                        <img :src="uploadedPhoto" id="dialogPhoto" style="max-height:100%; max-width:100%;">
                    </div>
                    <br>
                    <div class="text-center">
                        <p>    <strong></strong>  .</p>
                        <p>       <strong>AI </strong>  .</p>
                    </div>
                    <div>
                        <div class="btn_container">
                            <v-btn @click="downloadImage(uploadedPhoto)" class="mr-3"></v-btn>
                            <v-btn @click="goToAiPainter" class="ml-3">AI </v-btn>
                        </div>
                        <div>
                            <v-btn @click="handleDialogConfirmation" class="mt-3" color="primary"></v-btn>
                        </div>
                    </div>
                    </v-col>
                </v-row>
                </v-card-text>
            </v-card>
        </v-dialog>
    </div>
</template>

<script >
import EventBus from '@/EventBus.js';
import SvgIcon from '@jamescoyle/vue-icon';
import { mdiCameraOutline } from '@mdi/js';

export default {
    props: {
        show: {
            type: Boolean,
            required: true,
        },
        result4: {
            type: Boolean,
        }
    },
    components:{
        SvgIcon
    },
    data(){
      return {
        userResult: '', //   
        dialog: false,

        userResponse:'',
        uploadedPhoto: null, //     

        path: mdiCameraOutline,
      }
    },
    methods: {
        closeP(){
            this.$emit('close');
        },
        triggerCamera(){
            const photoFileInput = this.$el.querySelector("#photofile");
            photoFileInput.click();
        },
        onPhotoFileChange(event){
            let filename;
            if (window.FileReader) {
                filename = event.target.files[0].name;
            } else {
                filename = event.target.value.split('/').pop().split('\\').pop();
            }
            console.log("  : " + event.target.files[0].size);
            console.log(" : " + filename);
  
            this.previewImage(event.target);
  
              //    .
            this.uploadedPhoto = event.target.files[0];

              //    .
            const fileReader = new FileReader();
            fileReader.readAsDataURL(event.target.files[0]);
            fileReader.onload = () => {
                // const photoDataUrl = fileReader.result;
                // EventBus.$emit('add-photo', photoDataUrl);
                const photoDataUrl = fileReader.result;
                this.uploadedPhoto = photoDataUrl;
                EventBus.$emit('add-photo', photoDataUrl);
            }
        },
        previewImage(inputElement){
            if (inputElement.files && inputElement.files[0]) {
                let reader = new FileReader();
                reader.onload = (e) => {
                    this.$el.querySelector("#photoimg").setAttribute('src', e.target.result);
                    this.$el.querySelector("#photoimg").style.display = "block";
                }
                reader.readAsDataURL(inputElement.files[0]);
            }
        },
        submitResponse(){
            //   
            if (this.uploadedPhoto) {
                console.log(this.userResponse); //  
                this.userResponse = ''; //  

                this.dialog = true;

            } else {
                //     
                alert(" !");
            }
        },
        handleDialogConfirmation(){
            this.dialog = false;

            if (this.result4 === false){
                this.$emit('answerCorrect');
            }
            this.userResult = ''; //   
            this.closeP();        //  
        },
        goToAiPainter(){
            this.$router.push({name:'AIView'});
            if (this.result4 === false){
                this.$emit('answerCorrect');
            }
            this.userResult = '';
            this.closeP();

        },

        downloadImage(uploadedPhoto) {
            if (uploadedPhoto) {
                //    
                const a = document.createElement('a');
                a.href = uploadedPhoto;

                //     (: downloaded-image.jpg)
                const filename = `family_img`;
                a.download = filename;

                //     
                document.body.appendChild(a);
                a.click();
                document.body.removeChild(a);
            } else {
                //      (:    )
                alert('  !');
            }
        },

    }
}
</script>

<style scoped>
    body{
        margin: 0;
    }
    div{
        box-sizing: border-box;
    }
    .black-bg{
        width: 100%; height: 100%;
        background: rgba(0, 0, 0, 0.5);
        position: fixed; 
        top: 0;
        left: 0;
        padding: 20px;
        z-index: 1500;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .white-bg{
        width: 90%; 
        height: 90%;
        border-radius: 8px;
        position: fixed; 
        display: flex; 
        flex-direction: column; /*      */
        overflow-y: auto; /*       */

    }

    .content-container{
        width:100%;
        position: relative;
        top:8%;
    }
    .img-container{
        justify-content: center;
        display: flex;
        align-items: center;
    }
    #photoimg {
        display: none;
        width: 70%;
        height: auto; /*     */
        margin-top: 20px; /*    */
    }
    #photofile{
        display:None;
        /* background: yellow; */
        /* height: 50%; */
        /* width: auto; */
    }
    #dialogPhoto {
        width: 80%; /*    */
    }
    .quiz{
        flex: 1; /*       */
        display: flex; /* flex   */
        flex-direction: column; /*      */
        justify-content: center; /*   */
        align-items: center; /*   */
    }
    .quiz img{
        width: 100%;
        height: 100%;
        object-fit: cover; /*      */
    }
    .content {
        background-color: white;
        flex-grow: 1; /*      */
        display: flex;
        flex-direction: column;
        justify-content: flex-start; /*    */
        align-items: center;
        overflow-y: auto;
        padding-bottom: 20px; /*    */
    }
    .cam-container {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px; /*    */
    }
    .button-container {
        width: 100%; /*    */
        padding: 10px 20px; /*    */
        display: flex; /*    */
        justify-content: center; /*   */
        margin-top: auto; /*         */
    }
    .close {
        position: absolute; /*    */
        top: 10px; /*  10px    */
        right: 10px; /*  10px    */
        display: flex;
        width: auto; /*    */
        padding: 10px;
        background-color: #ccc;
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        justify-content: center;
    }
</style>s